*{
    user-select: none;
}

.rulesContainer {
    display: flex;
    justify-content: center;
    animation: fade 2s;
}

.rulesContainerHeader {
    display: flex;
    justify-content: center;
    top: 30%;
    color: white;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    user-select: none;
}

.rulesContainerItem {
    display: flex;
    justify-content: center;
    top: 60%;
}


.rulesContainerItem li img {
    
    color: white;
    background-color: rgba(0, 0, 0, 0.226);
    padding: 20px 20px;
    border-radius: 20px;
    transition: transform 0.7s;
}

.rulesContainerItem img:hover {
    transform: translateY(-20px);
    cursor: pointer;


}

@keyframes fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 100;
    }
}

.rulesContainerItem li {
    padding: 0 30px;
}