
.rulesContainer {
    display: flex;
    justify-content: center;
    animation: fade 2s;
}

.rulesContainerItemLi {

    color: white;
    display: flex;
    justify-content: center;
}

.img img {
    animation: fade 2s;
    display: flex;
    justify-content: center;
    border-radius: 20px;
    width: 400px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    color: white;
    transition: transform 0.7s;
    
}

.text {
    animation: fade 2s;    
}

.text p {
    animation: fade 2s;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
}

.text h1 {
    cursor: pointer;
    animation: fade 2s;
    position: relative;
}

.img img:hover{
    transform: translateY(-20px);
    cursor: pointer;
}

@keyframes fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 100;
    }
}

.rulesContainerHeader {  
    top: 100px;
    color: white;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    user-select: none;
    position: fixed;
}

.rulesContainerItem {
    top: 120px;
    padding: 100px 100px;
}

.rulesContainerItem li img {
    display: flex;
    justify-content: center;
    border-radius: 20px;
    width: 400px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    color: white;
    transition: transform 0.7s;
}

.App {
    font-family: sans-serif;
    text-align: center;
}

.main {
    display: flex;
    flex-direction:column;
   
    padding: 10px;  
      
}

