
*{
    user-select: none;
}

.rulesContainer {
    display: flex;
    justify-content: center;
    animation: fade 2s;
}

.rulesContainerItem {
    height: 50em;
    display: flex;
    justify-content: center;
    color: white;
    max-width: 1000px;
    top: 10%;
    background-color: rgba(0, 0, 0, 0.226);
    padding: 30px 30px;
    border-radius: 20px;
}

.rulesContainerItem li {
    padding: 0 30px;
}

@keyframes fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 100;
    }
}