* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  

}

ul {
  margin: 0;
  position: absolute;

}

ul li {
  display: inline;
  
}

a {
 
  width: 20%;
  padding: 0.5em 0.8em;
}

.item a {
  color: white;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
  text-decoration: none;
  font-size: 30px;
  width: 1px;
  height: 1px;
}

.item a.activeLink {
  color: rgb(133, 135, 255);
}

.item a:hover {
  color: rgb(133, 135, 255);
}

.logo {
  width: 6%;
  display: flex;
}